<template>
  <v-app>
    <v-content>
      <v-container>
        <v-card>
          <v-card-title>车辆模拟</v-card-title>
          <v-data-table :headers="[{text:'车牌号',value:'num'}]" :items="fileList" hide-actions>
            <template slot="items" slot-scope="props">
              <tr style="cursor:pointer">
                <td @click="num = props.item;submit()">{{ props.item }}</td>
              </tr>
            </template>
          </v-data-table>
          <v-card-text>
            <v-text-field v-model="num" label="请输入车牌号" @input="img = false"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-layout justify-end>
              <v-btn color="primary" @click="submit()">启动</v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
        <img v-if="img" :src="`/img/${num}`" class="mt-5">
      </v-container>
    </v-content>
  </v-app>

</template>

<script>
import axios from 'axios'
export default {
  name: 'app',
  data() {
    return {
      num: "",
      img: false,
      fileList: []
    }
  },
  async mounted() {
    this.fileList = (await axios.get('/img')).data
  },
  methods: {
    async submit() {
      try {
        await axios.get('/img/' + this.num.toString())
        this.img = true
      } catch (e) {
        alert('未获取到车牌信息')
      }

    }
  },
}
</script>

